@import url(https://fonts.googleapis.com/css2?family=Allura&family=Manrope&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


nav {
    min-height: 10vh;
    width: 100%;
    background-color: #7c8991;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo{
    margin-left: 20px;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: white;
}

.logo img{
    width: 70px;
}

.logo p{
    margin-left: 20px;
    font-size: 50px;
    font-family: 'Allura', cursive;
}

.nav-links{
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.nav-links a:hover{
    color: lightgrey;
}

.nav-links a{
    margin-right: 20px;
    padding: 20px 20px;
    display: block;
    color: white;
    text-align: center;
    text-decoration: none;
    font-family: 'Manrope', sans-serif;
    font-size: 16px;
}

.order_button a{
    background-color: #8A584C;
    border-radius: 50px;
    width: 174px;
}

.burger {
    display: none;
    margin-right: 20px;
}

.burger div {
    width: 25px;
    height: 3px;
    background-color: rgb(226, 226, 226);
    margin: 5px;
    transition: all 0.3s ease;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 1000px) {

    .burger {
        display: block;
        cursor: pointer;
    }

    .nav-links {
        position: absolute;
        height: auto;
        top: 9vh;
        background-color: #7c8991;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        z-index: 1;
        -webkit-animation: fadein 0.5s;
                animation: fadein 0.5s;
    }

    .nav-links a{
        margin-right: 0px;
    }

    .order_button a{
        background-color: #7c8991;
    }
}

.toggle .line1 {
    -webkit-transform: rotate(-45deg) translate(-5px, 6px);
            transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
    opacity: 0;
}

.toggle .line3 {
    -webkit-transform: rotate(45deg) translate(-5px, -6px);
            transform: rotate(45deg) translate(-5px, -6px);
}

@-webkit-keyframes fadein {
    from {
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}
footer {
    width: 100%;
    min-height: 10vh;
    background-color: #ACA69F;
    display: flex;
    align-items: center;
    justify-content: center;
}

footer div {
    font-size: 16px;
}
.hero-image{
  color: white;
  width:100%;
  height:90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient( rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4) ), url(/static/media/table.a30eed1f.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.hero-text h1{
  font-size: 5em;
  margin-bottom: 0.5em;
}

.hero-text p{
  font-size: 1.5em;
  margin-bottom: 0.25em;
}

.instagram {
  background-color: #F4E7D8;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 500px;
}

.instagram img{
  width: 300px;
}


